.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-right: 10px;
  padding-left: 10px;
  background-color: #E8EEF1; }

.container {
  display: flex;
  max-width: 770px;
  margin-right: auto;
  margin-left: auto; }

.container__inner {
  width: 770px;
  padding: 20px;
  margin-bottom: 15px;
  border-radius: 10px;
  border: 1px solid #D9DCE0;
  background-color: #fff; }

.container__wrapper {
  display: block;
  margin-bottom: 20px;
  width: 400px;
  margin-right: auto;
  margin-left: auto; }

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: auto;
  margin-left: auto;
  padding-bottom: 15px;
  background-color: #E8EEF1; }

.header__container {
  display: flex;
  flex-direction: column;
  max-width: 770px;
  margin-right: auto;
  margin-left: auto;
  padding: 20px;
  border-radius: 10px;
  border: 1px solid #D9DCE0;
  background-color: #fff; }

.header__header {
  margin: 0; }

.second {
  display: none; }

.third {
  display: none; }

.fourth {
  display: none; }

.button {
  width: 300px;
  height: 50px;
  padding: 10px;
  margin-right: auto;
  margin-left: auto;
  color: black;
  border-width: 1px;
  background-position: center center;
  text-transform: uppercase;
  font-weight: bold;
  border-color: #0cc8f2;
  border-style: solid;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  box-shadow: 0px 0px 30px 0px #0cc8f2; }

.submit {
  display: none; }

.form {
  display: flex;
  flex-direction: column;
  align-items: center; }

.form__radio-container {
  display: flex;
  flex-direction: column; }

.form__label {
  display: flex;
  flex-direction: column; }

.form__label-text {
  position: relative;
  margin-bottom: 15px;
  width: max-content; }
  .form__label-text::after {
    position: absolute;
    display: block;
    content: "*";
    color: red;
    font-size: 10px;
    top: 3px;
    right: -10px; }

.form__radio-button {
  position: relative;
  margin-bottom: 15px; }
  .form__radio-button:hover {
    outline: 3px solid black; }
  .form__radio-button .form__radio-leader {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden; }
    .form__radio-button .form__radio-leader:focus + .form__radio-text::after {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 728px;
      height: 178px;
      background-color: transparent;
      content: '';
      outline: auto 5px -webkit-focus-ring-color; }
    .form__radio-button .form__radio-leader:checked + .form__radio-text::after {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 728px;
      height: 178px;
      background-color: transparent;
      content: '';
      outline: 3px solid black; }

.form__search-field {
  display: block;
  width: 400px;
  padding: 10px;
  margin-bottom: 20px; }

.input {
  width: 300px;
  height: 25px;
  border: none;
  background-color: #fff;
  border-bottom: 1px solid #000; }

.next {
  position: relative;
  display: flex; }

.radio__header {
  position: relative;
  display: block;
  width: max-content; }
  .radio__header::after {
    position: absolute;
    display: block;
    content: "*";
    color: red;
    font-size: 10px;
    top: 3px;
    right: -10px; }

.invalid {
  position: absolute;
  left: 15px;
  top: 30px;
  display: block;
  width: 200px;
  font-size: 13px;
  font-weight: normal;
  color: red;
  padding-right: 5px;
  padding-left: 5px;
  height: auto;
  border: 1px solid red;
  border-radius: 10px;
  background-color: white; }
